.sortOptions{
    padding: 4rem;
    display: grid;
    min-width: 400px;

    gap: 3rem;  
}
.sortOptions__box{
    display: grid;
    /* margin-bottom: 2rem; */
}
/* -------------------------------- */
.sortOptions__box label{
    font-size: 2rem;
    padding: 1ch 0;
}
.rangeInput{
    --height: 1.5rem;
    /* -webkit-appearance: none;  */
    appearance: none;
    margin: .5rem 0;

    background: #fff;
    outline: none; 
    height: var(--height);

    border-radius: var(--height);

    box-shadow: 0px 0px .4rem rgba(0,0,0,.2);
}
.rangeInput::-webkit-slider-thumb {
    /* -webkit-appearance: none; */
    --size: 1.5rem;
    --bgc: #76bdff;
    --darkBgc: #2c80cf;
    appearance: none;
    width: var(--size);
    height: var(--size);
    background: #76bdff;
    cursor: pointer;

    box-shadow: inset 0 0 .6rem var(--darkBgc);

    border-radius: var(--size);
}
.rangeInput::-moz-range-thumb {
    width: 25px; /* Set a specific slider handle width */
    height: 25px; /* Slider handle height */
    background: #76bdff; /* Green background */
    cursor: pointer; /* Cursor on hover */
}

/* -------------------------------- */

.sortOptions__select{
    padding: 1ch;
}

/* -------------------------------- */
.sortOptions__btn{
    padding: 2ch;
    background-color: #d3d3d3;
    border: none;
    /* margin: 1rem 0; */

    transition-duration: .2s;
}
.sortOptions__btn:hover{
    background-color: #c0c0c0;
}
.sortOptions__btn:active{
    box-shadow: inset 0 0 1.5ch rgba(0,0,0,.2);
}