:root{
    --bg: #f5ebe0;
    --bg_alt: #e3d5ca;


    /* --c1:#edede9;
    --c2:#d6ccc2;
    --c3:#f5ebe0;
    --c4:#e3d5ca;
    --c5:#d5bdaf; */

    
    /* --c1:#606c38;
    --c2:#283618;
    --c3:#fefae0;
    --c4:#dda15e;
    --c5:#bc6c25; */

    --c1:#e0e0e0;
    --c2:#818181;
    --c3:#7c7c7c;
    --c4:#424242;
    --c5:#000000;
}

.app{
    width: 100vw;
    height: 100vh;

    background-color: #000000;

    display: grid;
    grid-template-rows: auto 1fr;
}

nav{
    width: 100%;
    height: 5rem;
}

main{
    width: 100%;
    display: grid;
    grid-template-columns: 1fr auto;
}

main > section{
    width: 100%;
    height: 100%;
}

.visualSection{
    background-color: var(--c1);
}
.settingsSection{
    background-color: var(--c2);
}